import state from './state'

const mutations = {
  SET_SIDEBAR_STATE: (isOpened) => {
    state.isSidebarOpened = isOpened
  },
  SET_SELECTED_PAGE: (page) => {
    state.selectedPage = page
  },
  SET_SELECTED_CHAPTER: (chapter) => {
    state.selectedChapter = chapter
  },
  SET_SELECTED_VOLUME: (volume) => {
    state.selectedVolume = volume
  },
  SET_SELECTED_MANGA: (manga) => {
    state.selectedManga = manga
  },
  SET_FULLPAGE_MODE: (isFullpage) => {
    state.isFullPageMode = isFullpage
  },
  SET_MULTIPAGE_MODE: (isMultipage) => {
    state.isMultipageMode = isMultipage
  },
  SET_UPLOADS_JSON: (json) => {
    state.uploadsJSON = json
  }
}

export default mutations