<template>
  <div>
    <div class="sidebarControl" @click="onSidebarControlClick" :class="{ movedWithSidebar: isSidebarOpened }">
      <i :class="{ fas: true, 'fa-angle-right': !isSidebarOpened, 'fa-angle-left': isSidebarOpened }"></i>
    </div>
    <div class="sidebar" :class="{ sidebarOpened: isSidebarOpened }">
      <div class="title">{{mangaName}}</div>
      <div class="controlsContainer">
        <div class="pageSwitch d-flex">
          <i class="fas fa-angle-left" @click="loadPreviousPage"></i>
          <b-form-select  v-model="selectedPageOption" :options="pageOptions" @change="setSelectedPage"></b-form-select>
          <i class="fas fa-angle-right" @click="loadNextPage"></i>
        </div>
        <b-form-select class="chapterSelect mt-3" v-model="selectedChapterOption" :options="chapterOptions" @change="setSelectedChapter"></b-form-select>
        <div class="volumeSelect d-flex mt-3">
          <b-form-select v-model="selectedVolumeOption" :options="volumeOptions" @change="setSelectedVolume"></b-form-select>
          <div class="stylingControls">
            <i
              :class="{ fas: true, 'fa-compress': !isFullPageMode, 'fa-expand': isFullPageMode }"
              @click="onClickFullPage"
            ></i>
            <i
              :class="{ fas: true, 'fa-long-arrow-alt-right': isMultipageMode, 'fa-long-arrow-alt-down': !isMultipageMode }"
              @click="onClickMultipage"
            ></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import store from '../store'

export default {
  name: 'fkts-sidebar',
  computed: {
    uploadsJSON() {
      return store.getters.uploadsJSON()
    },
    isSidebarOpened() {
      return store.getters.isSidebarOpened()
    },
    selectedPage() {
      return store.getters.selectedPage()
    },
    selectedChapter() {
      return store.getters.selectedChapter()
    },
    selectedVolume() {
      return store.getters.selectedVolume()
    },
    selectedManga() {
      return store.getters.selectedManga()
    },
    isFullPageMode() {
      return store.getters.isFullPageMode()
    },
    isMultipageMode() {
      return store.getters.isMultipageMode()
    },
  },
  data() {
    return {
      mangaName: '',
      selectedChapterOption: 0,
      chapterOptions: [],
      selectedVolumeOption: 0,
      volumeOptions: [],
      selectedPageOption: 0,
      pageOptions: []
    }
  },
  mounted() {
    this.getAllOptions()
    this.selectedChapterOption = this.selectedChapter
    this.selectedVolumeOption = this.selectedVolume
    this.selectedPageOption = this.selectedPage
  },
  watch: {
    selectedChapter() {
      this.selectedChapterOption = this.selectedChapter
      this.getAllOptions()
    },
    selectedVolume() {
      this.selectedVolumeOption = this.selectedVolume
      this.getAllOptions()
    },
    selectedPage() {
      this.selectedPageOption = this.selectedPage
    },
    selectedManga() {
      this.mangaName = this.selectedManga
    }
  },
  methods: {
    onSidebarControlClick() {
      if (!this.isSidebarOpened) store.actions.showReaderSidebar()
      else store.actions.hideReaderSidebar()
    },
    handleRouteChange() {
      this.$router.push(`/read/${this.selectedManga}/${this.selectedVolume}/${this.selectedChapter}/${this.selectedPage}`)
        .then(() => window.scrollTo(0, 0)) 
    },
    loadNextPage() {
      store.actions.setSelectedPage(this.selectedPage + 1)
        .then(() => this.handleRouteChange()) 
        .catch(() => this.$router.replace('/'))
    },
    loadPreviousPage() {
      store.actions.setSelectedPage(this.selectedPage - 1)
        .then(() => this.handleRouteChange())
        .catch(() => this.$router.replace('/'))
    },
    setSelectedPage() {
      store.actions.setSelectedPage(this.selectedPageOption)
        .then(() => this.handleRouteChange())
    },
    setSelectedChapter() {
      store.actions.setSelectedChapter(this.selectedChapterOption)
        .then(() => this.handleRouteChange())
    },
    setSelectedVolume() {
      store.actions.setSelectedVolume(this.selectedVolumeOption)
        .then(() => this.handleRouteChange())
    },
    onClickFullPage() {
      store.actions.setFullpageMode(!this.isFullPageMode)
    },
    onClickMultipage() {
      store.actions.setMultipageMode(!this.isMultipageMode)
    },
    getAllOptions() {
      this.chapterOptions = []
      this.volumeOptions = []
      this.pageOptions = []
      this.uploadsJSON.manga.forEach((manga) => {
        if (manga[this.selectedManga]) {
          this.mangaName = manga[this.selectedManga].name
          manga[this.selectedManga].volumes.forEach((vol) => {
            this.volumeOptions.push({ value: vol.id, text: `Vol. ${vol.id}` })
            if (vol.id === this.selectedVolume) {
              this.chapters = vol.chapters.length
              vol.chapters.forEach((ch) => {
                this.chapterOptions.push({ value: ch.id, text: `Ch. ${ch.id + 1} - ${ch.name}` })
                if (ch.id === this.selectedChapter) {
                  this.chapterName = ch.name
                  for (let i = 0; i < ch.pagesCount; i += 1) {
                    this.pageOptions.push({ value: i, text: i + 1})
                  }
                }
              })
            }
          })
        }
      })
    }
  }
}
</script>
<style scoped>
.sidebarControl {
  display: none;
}

.controlsContainer {
  display: flex;
  padding: 0px 10px 10px 0px;
  align-items: center;
}

.sidebar {
  width: 100vw;
  height: 110px;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #1c1c20;
  overflow-x: hidden;
  color: #cccccc;
  overflow: hidden;
}

.pageSwitch {
  text-align: center;
  height: 2em;
  margin-top: 1.5em;
  margin-bottom: 0.5em;
  align-items: center;
  flex: 1;
}

.pageSwitch  i {
  width: 50%;
  padding: 10px;
  cursor: pointer;
}

.pageSwitch i:hover {
  background: #27282d;
  color: #cccccc;
}


.pageSwitch .fa-angle-right {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.pageSwitch select {
  min-width: 40px;
}

.volumeSelect {
  flex: 1;
}

.volumeSelect select {
  min-width: 60px;
}


.chapterSelect {
  flex: 2;
  margin-right: 10px;
}

.title {
  height: 40px;
  padding: 0.5em;
  overflow: hidden;
  text-align: center;
}

.inactive {
  pointer-events: none;
}

.stylingControls {
  font-size: 20px; 
  display: flex;
  align-items: center;
}

.stylingControls i {
  cursor: pointer;
  margin-left: 10px;
}

.stylingControls .fa-compress {
  display: none;
}

@media screen and (min-width: 1025px) {
  .sidebarOpened {
    width: 250px!important;
  }
  .sidebarControl {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    transition: 0.5s;
    width: 40px;
    height: 40px;
    background: #1c1c20;
    color: white;
    cursor: pointer;
    line-height: 2;
    font-size: 20px;
    text-align: center;
    z-index: 2;
  }
  .sidebar {
    height: 100%;
    width: 0;
    position: fixed;
    transition: 0.5s;
  }
  .movedWithSidebar {
    z-index: 2;
    margin-left: 200px;
    transition: 0.5s;
  }
  .pageSwitch .fa-angle-right::after {
    font-weight: normal;
    content: "Next";
    margin-right: 1em; 
    font-family: sans-serif;
  }
  .pageSwitch .fa-angle-left::after {
    font-weight: normal;
    content: "Prev";
    margin-left: 1em; 
    font-family: sans-serif;
  }
  .pageSwitch select {
    width: 33%;
  }
  .volumeSelect select {
    width: 100px;
  }
  .stylingControls i {
    cursor: pointer;
    margin-left: 20px;
  }
  .controlsContainer {
    display: block;
    padding: 0px;
  }
  .stylingControls .fa-compress {
    display: block;
  }
  .chapterSelect {
    flex: 2;
    margin-right: 0;
  }
  .title {
    text-align: left;
  }
}
</style>