import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/fkts-homepage.vue'
import fktsReader from '../components/fkts-reader.vue'
import fktsNotFound from '../components/fkts-not-found.vue'
import fktsWiki from '../components/fkts-wiki.vue'
import fktsAbout from '../components/fkts-about.vue'
import fktsContact from '../components/fkts-contact.vue'
import store from '../store'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '*',
      component: fktsNotFound,
    },
    {
      path: '/read/:manga/:volume(\\d+)/:chapter(\\d+)/:page(\\d+)',
      name: 'reader',
      component: fktsReader,
      props: true,
    },
    {
      path: '/wiki',
      name: 'wiki',
      component: fktsWiki
    },
    {
      path: '/about',
      name: 'about',
      component: fktsAbout
    },
    {
      path: '/contact',
      name: 'contact',
      component: fktsContact
    },
  ],
  mode: 'history',
  base: '/snk/'
})

router.beforeEach((to, from, next) => {
  if (to.name === 'reader') {
    store.actions.setSelectedManga(to.params.manga)
      .then(() => store.actions.setSelectedVolume(Number(to.params.volume)))
      .then(() => store.actions.setSelectedChapter(Number(to.params.chapter)))
      .then(() => store.actions.setSelectedPage(Number(to.params.page)))
      .catch(() => next('/'))
  }
  next()
})

export default router