import mutations from './mutations'
import state from './state'

const findManga = (selectedManga) => {
  const json = state.uploadsJSON.manga.filter((manga) => manga[selectedManga])
  if (!json.length) throw new Error('Manga is not found')
  return json[0][selectedManga]
}

const findVolume = (manga, selectedVolume) => {
  const volume = manga.volumes.filter((vol) => vol.id === selectedVolume)
  if (!volume.length) throw new Error('Volume is not found')
  return volume[0]
}

const findChapter = (volume, selectedChapter) => {
  const chapter = volume.chapters.filter((ch) => ch.id === selectedChapter)
  if (!chapter.length) throw new Error('Chapter is not found')
  return chapter[0]
}

const handlePageChange = (page) => {
  const json = findManga(state.selectedManga)
  const volume = findVolume(json, state.selectedVolume)
  const chapter = findChapter(volume, state.selectedChapter)
  let newVol = state.selectedVolume
  let newCh = state.selectedChapter
  let newPage = page
  if (newPage >= chapter.pagesCount) {
    newPage = 0
    newCh += 1
  }
  if (newPage < 0) {
    newPage = 0
    newCh -= 1
  }
  const chapters = volume.chapters.map(ch => ch.id)
  if (newCh > Math.max(...chapters)) {
    newVol += 1
  }
  if (newCh < Math.min(...chapters)) {
    newVol -= 1
  }
  if (newVol >= json.volumes.length) throw new Error('Reached end of manga')
  if (newVol < 0) throw new Error('Back to start')
  return { page: newPage, chapter: newCh, volume: newVol}
}

const actions = {
  setUploadsJSON: json => new Promise((res) => {
    mutations.SET_UPLOADS_JSON(json)
    res()
  }),
  showReaderSidebar: () => new Promise((res) => {
    mutations.SET_SIDEBAR_STATE(true)
    res()
  }),
  hideReaderSidebar: () => new Promise((res) => {
    mutations.SET_SIDEBAR_STATE(false)
    res()
  }),
  setSelectedPage: page => new Promise((res) => {
    const newSettings = handlePageChange(page)
    mutations.SET_SELECTED_VOLUME(newSettings.volume)
    mutations.SET_SELECTED_CHAPTER(newSettings.chapter)
    mutations.SET_SELECTED_PAGE(newSettings.page)
    res()
  }),
  setSelectedChapter: chapter => new Promise((res) => {
    mutations.SET_SELECTED_PAGE(0)
    mutations.SET_SELECTED_CHAPTER(chapter)
    res()
  }),
  setSelectedVolume: volume => new Promise((res) => {
    const manga = findManga(state.selectedManga)
    const foundVolume = findVolume(manga, volume)
    mutations.SET_SELECTED_VOLUME(volume)
    mutations.SET_SELECTED_CHAPTER(foundVolume.chapters[0].id)
    mutations.SET_SELECTED_PAGE(0)
    res()
  }),
  setSelectedManga: manga => new Promise((res) => {
    mutations.SET_SELECTED_MANGA(manga)
    res()
  }),
  setFullpageMode: isFullpage => new Promise((res) => {
    mutations.SET_FULLPAGE_MODE(isFullpage)
    res()
  }),
  setMultipageMode: isMultipage => new Promise((res) => {
    mutations.SET_MULTIPAGE_MODE(isMultipage)
    res()
  })
}

export default actions