<template>
  <router-link
    tag="tr"
    :to="`/read/shingeki-no-kyojin/${volumeId}/${chapter.id}/0`"
    v-on:mouseover.native="hover = true"
    v-on:mouseleave.native="hover = false"
    v-bind:class="{ 'chapter-hover': hover }"
  >
    <th style="width: 25%;">Chapter {{ chapter.id }}</th>
    <td style="width: 75%;">{{ chapter.name }}</td>
  </router-link>
</template>
<script>
export default {
  name: 'fkts-chapter-item',
  data() {
    return {
      hover: false
    }
  },
  props: {
    chapter: Object,
    volumeId: String
  }
}
</script>
<style scoped>
.chapter-hover {
  background-color: rgba(30, 30, 30, 1);
  cursor: pointer;
}
</style>
