<template>
  <div class="fkts-reader">
    <fkts-sidebar></fkts-sidebar>
    <div :class="{ movedWithSidebar: isSidebarOpened }"
        @click="loadNextPage">
      <div class="w-100 imageWrapper">
          <div v-if="!isMultipageMode" :class="{ fullpage: isFullPageMode }">
            <b-img
              :src="`/manga/${selectedManga}/${selectedVolume}/${selectedChapter}/${selectedPage}.png`"
              center
              fluid
            >
            </b-img>
          </div>
          <div v-for="pageIndex in pages" :key="pageIndex" v-else :class="{ fullpage: isFullPageMode }">
            <b-img
              :src="`/manga/${selectedManga}/${selectedVolume}/${selectedChapter}/${pageIndex - 1}.png`"
              center
              fluid
            >
            </b-img>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
import fktsSidebar from './fkts-sidebar'
import store from '../store'

export default {
  name: 'fkts-reader',
  components: {
    fktsSidebar
  },
  data() {
    return {
      pages: 0,
      mangaName: '',
      chapterName: '',
      // baseURL: 'http://localhost:8080',
    }
  },
  created() {
    this.getAllPages()
  },
  computed: {
    uploadsJSON() {
      return store.getters.uploadsJSON()
    },
    isSidebarOpened() {
      return store.getters.isSidebarOpened()
    },
    selectedPage() {
      return store.getters.selectedPage()
    },
    selectedChapter() {
      return store.getters.selectedChapter()
    },
    selectedVolume() {
      return store.getters.selectedVolume()
    },
    selectedManga() {
      return store.getters.selectedManga()
    },
    isFullPageMode() {
      return store.getters.isFullPageMode()
    },
    isMultipageMode() {
      return store.getters.isMultipageMode()
    },
  },
  methods: {
    loadNextPage() {
      store.actions.setSelectedPage(this.selectedPage + 1)
        .then(() => this.$router.push(`/read/${this.selectedManga}/${this.selectedVolume}/${this.selectedChapter}/${this.selectedPage}`))
        .then(() => window.scrollTo(0, 0))
        .catch(() => this.$router.replace('/'))
    },
    getAllPages() {
      this.uploadsJSON.manga.forEach((manga) => {
        if (manga[this.selectedManga]) {
          manga[this.selectedManga].volumes.forEach((vol) => {
            if (vol.id === this.selectedVolume) {
              vol.chapters.forEach((ch) => {
                if (ch.id === this.selectedChapter) {
                  this.chapterName = ch.name
                  this.pages = ch.pagesCount
                }
              })
            }
          })
        }
      })
    }
  }
}
</script>
<style scoped>
.movedWithSidebar {
  margin-left: 250px;
}
.fkts-reader {
  min-height: 100vh;
  background: #303037;
}

.imageWrapper {
  cursor: pointer;
  padding-top: 120px;
}

.fullpage {
   height: 100vh;
}

.fullpage img {
  width: auto;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
}


@media screen and (min-width: 1025px) {
  .imageWrapper {
    cursor: pointer;
    padding-top: 0px;
  }
}
</style>